#about {
    background-color: white;
}

.about__container {
    width: 85%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10%;
    opacity: 0;
    filter: blur(5px);
    transform: translateX(-30%);
    transition: all 1s;
}

.about__me {
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-dark);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-dark);
}

.animated-x {    
    opacity: 1;
    transform: none;
    filter: blur(0);
    transform: translateX(0);
}

/* Media queries (Medium devices) */
@media screen and (max-width: 1024px) {
    .about__container {
        width: 95%;
        grid-template-columns: 1fr;
        grid-template-rows: 2fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}
  
/* Media queries (Small devices) */
@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0rem auto 3rem;
        order: 2;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
    }

    .about__content {
        text-align: center;
        order: 1;
        padding-bottom: 5rem;
    }

    .about__content p {
        margin: 1.5rem 0;
    }

}